.global-list{
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 0;
  list-style: none;
} 
  .global-list li{
    position: relative;
    padding-left: 20px;
  }
    .global-list li::before{
      content: '\2014';
      display: inline-block;

      position: absolute;
      top: 0; left: 0;
      z-index: 2;
    }


